body #root {
  width: 600px;
  height: 600px;
  color: #ff6700;
  font-size: 32px;
  background: url(/assets/1.3e3dcc2c7c48f625f637db60ee654b90.jpg);
  -webkit-animation: test 1s linear alternate infinite;
  animation: test 1s linear alternate infinite; }

@-webkit-keyframes test {
  0% {
    font-size: 36px; }
  50% {
    font-size: 50px; }
  100% {
    font-size: 18px; } }

@keyframes test {
  0% {
    font-size: 36px; }
  50% {
    font-size: 50px; }
  100% {
    font-size: 18px; } }
